<template>
  <div>
    <form v-if="login" @submit.prevent="submitAction" id="enroll-form" name="enroll-form" class="enroll-form forms">

      <h2 v-translate>Enrollment</h2>

      <div v-if="formSubmitted">
        <h3 v-translate>Congratulation</h3>
        <p><span v-translate>Successfully</span> {{event.title}}</p>
      </div>
      <div v-if="!formSubmitted">
        <button type="submit" class="button-primary" v-bind:disabled="formSubmitted" v-translate>Enroll</button>
      </div>
    </form>
    <div v-if="!login">
      <h2 v-translate>Enrollment</h2>
      <div v-if="hideRegister">
        <login-form></login-form>
        <button @click="hideRegister = !hideRegister" class="sign-in" v-translate>Register</button>
      </div>
      <div v-if="!hideRegister">
        <register-form></register-form>
        <button @click="hideRegister = !hideRegister" class="sign-in" v-translate>Login</button>
      </div>
  </div>
</template>

<style>
  .modal-container form.enroll-form {
    padding: 0;
    margin:2.5rem 0 0 0;
  }
  .event .modal-container .form  {
    padding: 0;
  }
  .event .modal-container .form .col-full input,
  .event .modal-container .form .col-1,
  .event .modal-container .form .col-2 {
    margin-bottom: 1.2rem;
  }
  .event .modal-container .form label.label-inline {
    position: relative;
    top: 9px;
  }
</style>

<script>
  import { mapState } from 'vuex'
  import LoginForm from './LoginForm'
  import RegisterForm from './RegisterForm'

  export default {
    props: ['event'],
    components: {
      LoginForm,
      RegisterForm
    },
    data: function() {
      return {
        mail: '',
        formSubmitted: false,
        hideRegister: true
      }
    },
    computed: {
      ...mapState({
        routeParams (state){
          return state.route.params;
        },
        login(state) {
          return !!state.user.username;
        }
      })
    },
    methods: {
      submitAction (e) {
        this.$store.dispatch('saveSubscription', {
          event_id: this.event.id,
          user_id: this.$store.state.user.id
        })
          .then(() => {
            this.formSubmitted = true;
          })
      }
    }
  }
</script>



