import curry from 'lodash/fp/curry'
import reduce from 'lodash/reduce'

const createSection = curry((type, data) => {
  return {
    component: type === 'separator' ? 'timeline-separator' : 'timeline-event',
    type,
    data: Object.assign({}, data)
  }
})

const createSeparator = createSection('separator');
const createEvent = createSection('event');

function getSectionDate(mode, date) {
  const objDate = new Date(date);
  switch (mode) {
    case 'year':
      objDate.setMonth(0);
      objDate.setDate(1);
      objDate.setHours(0, 0, 0);
      break;
    case 'month':
      objDate.setDate(1);
      objDate.setHours(0, 0, 0);
      break;
    case 'week':
      objDate.setDate(objDate.getDate() - objDate.getDay());
      objDate.setHours(0, 0, 0);
      break;
    default:
      objDate.setHours(0, 0, 0);
      break;
  }
  return objDate.getTime()
}

let previousSectionDate;

const reduceEvents = function (events, timelineMode) {
  return reduce(events, (result, value) => {
    let currentSectionDate = getSectionDate(timelineMode, value.startDate);

    if (!previousSectionDate) {
      result.push(createSeparator({ date: currentSectionDate }));
    }
    if (previousSectionDate && previousSectionDate !== currentSectionDate) {
      result.push(createSeparator({date: currentSectionDate, prevDate: previousSectionDate}));
    }
    result.push(createEvent(value));
    previousSectionDate = currentSectionDate;
    return result;
  }, [])
};

export default reduceEvents



